'use client';

import React, { useState } from 'react';
import { Bar } from 'react-chartjs-2';
import 'chart.js/auto';
import { Search, Filter } from 'lucide-react';
import { useRouter } from 'next/navigation';
import UserDetails from './UserDetails';

const dummyUsers = Array.from({ length: 30 }, (_, i) => ({
  id: i + 1,
  name: `user00${i + 1}`,
  role: i % 3 === 0 ? 'A' : 'U',
  email: `user${i + 1}@example.com`,
  phone: `+34 11111111${i}`,
  surname: 'Zajaceva',
  address: 'Av. de Madrid',
  city: 'Jaen',
  subtype: ['VIP', 'Standard', 'Premium'][i % 3],
}));

const chartData = {
  labels: ['2019', '2020', '2021', '2022', '2023', '2024', '2025'],
  datasets: [
    {
      label: 'Users per year',
      backgroundColor: '#33b5aa',
      borderRadius: 5,
      data: [5, 10, 15, 20, 30, 45, 60],
    },
  ],
};

const options = {
  responsive: true,
  plugins: {
    legend: {
      display: false,
    },
  },
};

export default function AdminMainPage() {
  const [selectedUserId, setSelectedUserId] = useState(null);
  const [showFilter, setShowFilter] = useState(false);
  const [filterRoles, setFilterRoles] = useState({});
  const [filterSubtypes, setFilterSubtypes] = useState({});
  const router = useRouter();

  const selectedUser = dummyUsers.find(user => user.id === selectedUserId);

  const handleRoleFilterChange = (role) => {
    setFilterRoles((prev) => ({ ...prev, [role]: !prev[role] }));
  };

  const handleSubtypeFilterChange = (subtype) => {
    setFilterSubtypes((prev) => ({ ...prev, [subtype]: !prev[subtype] }));
  };

  const clearFilter = () => {
    setFilterRoles({});
    setFilterSubtypes({});
  };

  const filteredUsers = dummyUsers.filter(user => {
    const roleMatch = Object.keys(filterRoles).length === 0 || filterRoles[user.role];
    const subtypeMatch = Object.keys(filterSubtypes).length === 0 || filterSubtypes[user.subtype];
    return roleMatch && subtypeMatch;
  });

  return (
    <div className="flex w-full h-screen bg-[#fdf9f3] overflow-hidden">
      {/* Левая панель */}
      <div className="w-[300px] p-4 border-r border-gray-200 flex flex-col">
        <div className="relative mb-4">
          <input
            type="text"
            placeholder="Search"
            className="w-full pl-8 pr-10 py-2 rounded bg-gray-100 text-sm"
          />
          <Search size={16} className="absolute left-2 top-2.5 text-gray-400" />
          <Filter
            size={16}
            className="absolute right-2 top-2.5 text-gray-400 cursor-pointer"
            onClick={() => setShowFilter(!showFilter)}
          />
          {showFilter && (
            <div className="absolute top-10 left-0 w-full bg-gray-500 text-white rounded p-3 text-sm z-10">
              {['A', 'U'].map(role => (
                <div className="flex items-center mb-2" key={role}>
                  <input type="checkbox" className="mr-2" checked={filterRoles[role] || false} onChange={() => handleRoleFilterChange(role)} />
                  <label>{role === 'A' ? 'Admin' : 'User'}</label>
                </div>
              ))}
              <hr className="my-2" />
              {['VIP', 'Standard', 'Premium'].map(subtype => (
                <div className="flex items-center mb-2" key={subtype}>
                  <input type="checkbox" className="mr-2" checked={filterSubtypes[subtype] || false} onChange={() => handleSubtypeFilterChange(subtype)} />
                  <label>{subtype}</label>
                </div>
              ))}
              <button className="w-full bg-gray-300 text-black rounded py-1 mt-2" onClick={clearFilter}>
                Clear filter
              </button>
            </div>
          )}
        </div>

        <button
          className="mb-4 py-2 bg-[#33b5aa] text-white rounded"
          onClick={() => router.push('/add-user')}
        >
          Add user
        </button>

        <div className="overflow-y-auto flex-1">
          {filteredUsers.map(user => (
            <div
              key={user.id}
              onClick={() => setSelectedUserId(user.id)}
              className={`flex items-center justify-between px-2 py-2 mb-1 rounded cursor-pointer ${
                selectedUserId === user.id ? 'bg-gray-300' : 'bg-gray-200'
              }`}
            >
              <div className="flex items-center gap-2">
                <img
                  src="/images/user.jpg"
                  className="w-6 h-6 rounded-full"
                  alt="avatar"
                />
                <span className="text-sm">{user.name}</span>
              </div>
              <div className="text-xs px-2 py-0.5 bg-gray-100 rounded">
                {user.role}
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Правая часть */}
      <div className="flex-1 p-6 overflow-hidden flex flex-col">
        <h2 className="text-xl font-semibold text-center mb-4">
          Welcome to JaenGym admin table
        </h2>

        <div className="flex flex-col gap-4 flex-1 overflow-hidden">
          {/* Статистика */}
          <div className="h-[230px]">
            <Bar data={chartData} options={options} />
            <div className="text-center mt-2">
              <button className="text-blue-500 underline hover:text-blue-700">
                See full statistics
              </button>
            </div>
          </div>

          {/* Детали пользователя */}
          <div className="bg-gray-300 p-4 rounded overflow-auto h-[230px]">
            {selectedUser ? (
              <UserDetails user={selectedUser} />
            ) : (
              <p className="text-center">Select a user for more</p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}


